import { redirect } from "react-router-dom";

export function getTokenDuration() {
    const savedExpirarionDate = localStorage.getItem("expiration");
    const expirationDate = new Date(savedExpirarionDate);
    const now = new Date();
    const duration = expirationDate.getTime() - now.getTime();
    return duration;
}
/*
 * Token
 */
export function getAuthToken() {
    const token = localStorage.getItem('token');
    if (!token) {
        return null;
    }
    /* const tokenDuration = getTokenDuration();
    if (tokenDuration < 0) {
        return 'EXPIRED';
    } */
    return token;
}



export function tokenLoader() {
    return getAuthToken();
}



export function checkTokenLoader() {
    const token = getAuthToken();
    if (!token) {
        return redirect("/login");
    }

    return null;
    
}

export function getForcedResetStatus() {
    const isReset = localStorage.getItem("isReset");
    if (!isReset || isReset === "1") {
        return null;
    }
    return isReset;
}





