import React, { useEffect, useState } from "react";
import { useSubmit, useActionData, useNavigation, json } from "react-router-dom";
import AlertCard from "../../UI/MessageBox/AlertCard";
import DatePicker, { registerLocale, setDefaultLocale } from "react-datepicker";
import de from "date-fns/locale/de";
import "react-datepicker/dist/react-datepicker.module.css";
import Button from "../../UI/Button/Button";
import { getAuthToken } from "../../utils/auth";
import CustomTooltip from "../../UI/Tooltip/Tooltip";


registerLocale("de", de);
setDefaultLocale("de");

function CreateTokenForm(props) {
  const actionData = useActionData();
  //const navigation = useNavigation();
  //const submit = useSubmit();

  const [enterTokenName, setEnterTokenName] = useState("");
  const [inputMaxJobsValue, setInputMaxJobsValue] = useState("1");
  const [inputMaxBudgetsValue, setInputMaxBudgetsValue] = useState("1");
  const [inputValidityValue, setInputValidityValue] = useState("1");
  const [creation, setCreation] = useState(new Date());
  const [expiration, setExpiration] = useState("");

  const [formIsValid, setFormIsValid] = useState(false);
  const [error, setError] = useState(null);
  const [isSubmitting, setIsSubmitting] = useState(null);
  //const isSubmitting = navigation.state === "submitting";
  
  

  useEffect(() => {
    if ( enterTokenName.trim().length > 0) {
      setFormIsValid(true);
    } else {
      setFormIsValid(false);
    }
    updateExpiration(creation, inputValidityValue);
  }, [enterTokenName, creation, inputValidityValue]);

  // token_name
  const tokenNameChangeHandler = (event) => {
    //setIsFormTouched(true);
    setEnterTokenName(event.target.value);
  };
  // max_nb_jobs
  const jobChangeHandler = (event) => {
    //setIsFormTouched(true);
    setInputMaxJobsValue(event.target.value);
  };
  // max_budgets
  const budgetChangeHandler = (event) => {
    //setIsFormTouched(true);
    setInputMaxBudgetsValue(event.target.value);
  };
  // validity
  const validityChangeHandler = (event) => {
    //setIsFormTouched(true);
    setInputValidityValue(event.target.value);
    updateExpiration(creation, event.target.value);
  };
  // creation
  const creationChangeHandler = (date) => {
    //setIsFormTouched(true);
    setCreation(date);
    updateExpiration(date, inputValidityValue);
  };
  // expiration
  const updateExpiration = (creation, inputValidityValue) => {
    let expiredMsec =
      Date.parse(creation) + parseInt(inputValidityValue) * 86400000;
    const options = {
      day: "2-digit",
      month: "2-digit",
      year: "numeric",
    };
    let expiredDate = new Date(expiredMsec).toLocaleString("de-DE", options);
    setExpiration(expiredDate);
  };

  // submit form handler to Backend API
  

  const createTokenHandler = async (event) => {
    event.preventDefault();
    setIsSubmitting(true);
    //setIsFormTouched(true);

    // check form is valid
    if (enterTokenName.trim().length === 0) {
      setFormIsValid(false);
      setError("Token name must not be empty.");
      setIsSubmitting(false);
      return;
    }

    const tokenData = {
      token_name: enterTokenName,
      validity: inputValidityValue,
      //creation: creation,
      //expiration: expiration,
      max_nb_jobs: inputMaxJobsValue,
      max_budget: inputMaxBudgetsValue,
    };

    // submit form to router
    //submit({ tokenData: tokenData }, { method: "POST", action: "tokens" });

    // submit to backend API
    const access_token = getAuthToken();
    const tokens_url = "http://portal2.quantum.lrz.de:5000/tokens";
    try {
      const response = await fetch(tokens_url, {
        method: "POST",
        headers: {
          Authorization: "Bearer " + access_token,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(tokenData),
      });

      const resData = await response.json();
      setIsSubmitting(false);
      if (!response.ok) {
        return resData.error_message;
      } else {
        console.log(resData.token_data);
        // TO DO: update tokens list with new token item
        
        return resData.token_data;
      }
      
    } catch (error) {
      throw json(error);
    }

    // clear input form
    setEnterTokenName("");
    setInputMaxJobsValue("1");
    setInputMaxBudgetsValue("1");
    setInputValidityValue("1");
    setCreation(new Date());
    setExpiration("");

    setFormIsValid(false);
    setError(null);
    setIsSubmitting(null);
  };


  const hideTokenFormHandler = () => {
    props.onShowing(false);
  };

  const clearError = () => {
    setError(null);
  }

  

  return (
    <React.Fragment>
      <div className="container_header_wrap">
        <h4 className="page_header">Create Token</h4>
        <button onClick={hideTokenFormHandler} className="closeTokenForm_btn">
          <span className="close_icon"></span>
        </button>
      </div>
      <div className="mb-4 tokenForm_container">
        <p className="form_indication_text">(*) Required field</p>
        {!formIsValid && error && (
          <AlertCard variant="danger" onClear={clearError}>{error}</AlertCard>
        )}
        <form id="token_form" onSubmit={createTokenHandler} method="POST">
          {actionData && actionData.errors && (
            <AlertCard variant="danger">
              <ul>
                {Object.values(actionData.errors).map((err) => (
                  <li key={err}>{err}</li>
                ))}
              </ul>
            </AlertCard>
          )}
          <div className="row mx-0 ">
            <div className="col-12 col-xl-6 ">
              <div className="form-group mb-3 token_input">
                <label htmlFor="token_name" className="form-label">
                  Token Name *
                </label>
                <input
                  type="text"
                  id="token_name"
                  name="token_name"
                  className={`${
                    error && enterTokenName.trim().length === 0
                      ? "form-control invalid_input"
                      : "form-control token_name_field"
                  }`}
                  value={enterTokenName}
                  onChange={tokenNameChangeHandler}
                />
                {error && enterTokenName.trim().length === 0 && (
                  <p className="error-text">
                    The token name must not be empty!
                  </p>
                )}
              </div>
              <div className="form-group mb-3 token_input">
                <label htmlFor="validity" className="form-label">
                  Validity *
                </label>
                <input
                  type="range"
                  min="1"
                  max="7"
                  step="1"
                  id="validity"
                  name="validity"
                  className="form-range"
                  value={inputValidityValue}
                  onChange={validityChangeHandler}
                />
                <div className="range_value_wrap">
                  <span id="validity_value">{inputValidityValue}</span>
                  <span>/7 day(s)</span>
                </div>
              </div>
              <div className="form-group mb-3 tokeninput">
                <label htmlFor="creation" className="form-label">
                  Valid From *
                </label>
                <DatePicker
                  id="creation"
                  name="creation"
                  className="form-control token_name_field"
                  dateFormat={"dd.MM.yyyy"}
                  selected={creation}
                  onChange={creationChangeHandler}
                />
              </div>
              <div className="form-group mb-3 tokeninput">
                <label htmlFor="expiration" className="form-label">
                  Expiration (read only)
                </label>
                <input
                  type="text"
                  id="expiration"
                  name="expiration"
                  className="form-control token_name_field"
                  value={expiration}
                  readOnly
                />
              </div>
            </div>
            <div className="col-12 col-xl-6 ">
              <div className="form-group mb-3 token_input">
                <CustomTooltip
                  id="nb_job_tooltip"
                  tooltip="The number of jobs that token is used for"
                  placement="top"
                >
                  <label htmlFor="max_jobs" className="form-label">
                    Maximum Job Count *
                  </label>
                </CustomTooltip>
                <input
                  type="range"
                  min="1"
                  max="100"
                  id="max_jobs"
                  name="max_jobs"
                  className="form-range"
                  value={inputMaxJobsValue}
                  onChange={jobChangeHandler}
                />
                <div className="range_value_wrap">
                  <span id="max_jobs_value">{inputMaxJobsValue}</span>
                  <span>/100</span>
                </div>
              </div>
              <div className="form-group mb-3 token_input">
                <CustomTooltip
                  id="max_budget_tooltip"
                  tooltip="The maximum budget comsumption allowed for this token."
                  placement="top"
                >
                  <label htmlFor="max_budget" className="form-label">
                    Maximum Budget Usage *
                  </label>
                </CustomTooltip>
                <input
                  type="range"
                  id="max_budget"
                  name="max_budget"
                  min="1"
                  max="100000"
                  className="form-range"
                  value={inputMaxBudgetsValue}
                  onChange={budgetChangeHandler}
                />
                <div className="range_value_wrap">
                  <span id="max_budgets_value">{inputMaxBudgetsValue}</span>
                  <span>/100000</span>
                </div>
              </div>
            </div>
          </div>
          <div className="row mx-0">
            <div className="col-12 my-4 actions">
              <Button
                type="submit"
                className="createToken_btn"
              >
                {isSubmitting ? "Submitting..." : "Submit"}
              </Button>
              <Button
                className="gray_btn cancel_btn"
                onClick={hideTokenFormHandler}
              >
                Cancel
              </Button>
            </div>
          </div>
        </form>
      </div>
    </React.Fragment>
  );
}

export default CreateTokenForm;