import React from "react";
import { Link } from "react-router-dom";
import ActivedTokenListItem from "./ActivedTokenListItem";
import RevokedTokenListItem from "./RevokedTokenListItem";

function TokensList(props) {
  
  const activatedTokens = null;
  const revokedTokens = null;
  if (props.items.length === 0) {
    return <h4 className="text-center my-3">No Token issued</h4>;
  }
  else {
    activatedTokens = props.items.filter(
      (token) => token.status === "Active"
    );
    revokedTokens = props.items.filter(
      (token) => token.status === "Revoked"
    );
    //console.log(activatedTokens);

  }
  

  return (
    <React.Fragment>
      {activatedTokens && activatedTokens.length > 0 && (
        <div className="mb-5 activatedTokensList">
          <h5>Actived Tokens</h5>
          <div className="responsive_container">
            <ul className="subTokensList">
              <li className="token_row_header">
                <div className="token_column token_view"></div>
                <div className="token_column token_name">Token Name</div>
                <div className="token_column token_expired">Expiration</div>
                <div className="token_column token_note">Note</div>
                <div className="token_column token_actions"></div>
              </li>
              {activatedTokens.map((token) => (
                <ActivedTokenListItem
                  key={token.id}
                  id={token.id}
                  tokenName={token.token_name}
                  expiration={token.expiration}
                  note={token.note}
                />
              ))}
            </ul>
          </div>
        </div>
      )}
      {revokedTokens && revokedTokens.length > 0 && (
        <div className="mb-5 revokedTokensList">
          <h5>Revoked Tokens</h5>
          <div className="responsive_container">
            <ul className="subTokensList">
              <li className="token_row_header">
                <div className="token_column token_view"></div>
                <div className="token_column token_name">Token Name</div>
                <div className="token_column token_expired">Revoked Time</div>
                <div className="token_column token_note">Revoked Reason</div>
                <div className="token_column token_actions"></div>
              </li>
              {revokedTokens.map((token) => (
                <RevokedTokenListItem
                  key={token.id}
                  id={token.id}
                  tokenName={token.token_name}
                  revokedTime={token.revoked_timestamp}
                  revoked_reason={token.revoked_reason}
                />
              ))}
            </ul>
          </div>
        </div>
      )}
    </React.Fragment>
  );
}


export default TokensList;