import React, { useEffect, useState } from "react";
import { NavLink, Form, useRouteLoaderData } from "react-router-dom";
import { getForcedResetStatus } from "../../utils/auth";

import Logout from "../../Pages/Logout";
const UserNavigation = () => {
  const [isForcedReset, setIsForcedReset] = useState(true);
  const user_token = useRouteLoaderData("home");
  useEffect(() => {
    const isReset = getForcedResetStatus();
    if (isReset && isReset === "0") {
      setIsForcedReset(false);
    }
  }, []);
    return (
      <div className="user_acc">
        <ul className="mainNav userNav">
          {user_token && !isForcedReset && (
            <li className="nav-item ">
              <NavLink
                to="settings"
                title="Account Settings"
                className="nav-link "
                {...({ isActive }) => (isActive ? "active" : undefined)}
                end
              >
                <span className="link_icon settings_icon"></span>
                Settings
              </NavLink>
            </li>
          )}

          <li className="nav-item ">
            <Logout />
          </li>
        </ul>
      </div>
    );
}

export default UserNavigation;