import React, { useState, useEffect } from "react";
import { useRouteLoaderData } from "react-router-dom";
import NavbarHeader from "./NavbarHeader";
import Navigation from "./Navigation";
import UserNavigation from "./UserNav";
import { getForcedResetStatus } from "../../utils/auth";


function MainNavigation(props) {
  const [isForcedReset, setIsForcedReset] = useState(true);
  const access_token = useRouteLoaderData('home');
  
  useEffect(() => {
    const isReset = getForcedResetStatus();
    if (isReset && isReset === "0") {
      setIsForcedReset(false);
    }
  }, []);
	return (
    <div className="left_sidebar">
      <NavbarHeader />
      <div className="main_navigation" id={props.id ? props.id : " "}>
        <div className="left_navbar">
          {access_token && !isForcedReset && <Navigation />}

          <UserNavigation />
        </div>
      </div>
    </div>
  );

}

export default MainNavigation;