import React, { useContext, Suspense, useState } from "react";
import { useLoaderData, defer, Await, json } from "react-router-dom";
import ContentCard from "../../UI/Card/ContentCard";
import BlankCard from "../../UI/Card/BlankCard";
import ThemeContext from "../../../store/theme-context";
import TokensList from "./TokensList";
import NewToken from "./NewToken";
import { getAuthToken } from "../../utils/auth"; 

import './Tokens.scss';

function Tokens() {
  const ctx = useContext(ThemeContext);

  const [error, setError] = useState(null);

  const loaderData = useLoaderData();
  console.log("loaded data:");
  console.log(loaderData);

  if (loaderData.msg) {
    setError(loaderData.msg);
  }
  
  const tokens = loaderData.tokens;
  
  // transform token format by adding id
  // const transformedTokens = tokens.map((token) => ({
  //   id: token.token_name,
  //   token_name: token.token_name,
  //   status: token.status,
  //   note: token.note,
  // }));
  
  
  
  return (
    <React.Fragment>
      <div className="main_content">
        <NewToken />
        <ContentCard
          className={`${
            ctx.darkmode ? "dark_bg" : "white_bg"
          } tokens_container`}
        >
          {tokens && tokens.length === 0 && (
            <BlankCard>
              <h6>No tokens found!</h6>
            </BlankCard>
          )}
          {tokens && tokens.length > 0 && (
            <div className="listTokens_container">
              <div className="tokenContainer_header_wrap">
                <h4 className="mb-5 page_header">All Tokens</h4>
              </div>
              <div className="tokensList_container">
                <TokensList items={tokens} />
                {/* 
              <Suspense
                fallback={<p style={{ textAlign: "center" }}>Loading...</p>}
              >
                <Await resolve={transformedTokens}>
                  {(loadedtokens) => <TokensList items={loadedtokens} />}
                </Await>
              </Suspense>
              */}
              </div>
            </div>
          )}
        </ContentCard>
      </div>
    </React.Fragment>
  );
}

export default Tokens;

/*
 * GET all tokens action
 */
export async function loader() {
  // fetching to tokens
  const tokens_url = "http://portal2.quantum.lrz.de:5000/tokens";
  const access_token = getAuthToken();
  try {
    const response = await fetch(tokens_url, {
      method: "GET",
      headers: {
        Authorization: "Bearer " + access_token,
        "Content-Type": "application/json",
      },
    });
    const data = await response.json();
    console.log("responsed data:");
    console.log(data);

    if (!response.ok) {
      return data.error_message || data.msg;
    } else {
      return data.tokens;
    }
  } catch (error) {
    console.log("Catch errors:");
    console.log(error);
    throw json(error);
  }
}

